.SideBar {
    width: 100%;
    height: 100vh;
    padding: 20px 30px;
    transition: .5s;
    z-index: 2;
}

.SideBar .la-times {
    display: none;
    transition: .5s;
}

.SideBar .logo {
    font-family: Poppins;
    font-weight: bold;
    color: #135bcc;
}

@media only screen and (max-width: 1201px) {
    .SideBar {
        position: fixed;
        width: 30vw;
        top: 0;
        right: 100%;
        display: none;
        background-color: #f2f4f8;
        box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    }

    .showSidebar {
        display: block;
        animation: SidebarMove1201 .3s linear 1 forwards;
    }
}

@keyframes SidebarMove1201 {
    100% {
        right: 70vw;
    }
}

@media only screen and (max-width: 992px) {
    .SideBar {
        width: 40vw;
    }

    .showSidebar {
        animation: SidebarMove992 .3s linear 1 forwards;
    }
}

@keyframes SidebarMove992 {
    100% {
        right: 60vw;
    }
}

@media only screen and (max-width: 768px) {
    .SideBar {
        width: 50vw;
    }

    .showSidebar {
        animation: SidebarMove768 .3s linear 1 forwards;
    }
}

@keyframes SidebarMove768 {
    100% {
        right: 50vw;
    }
}

@media only screen and (max-width: 600px) {
    .SideBar {
        width: 100vw;
        padding-top: 50px;
    }

    .showSidebar {
        animation: SidebarMove600 .3s linear 1 forwards;
    }

    .SideBar .la-times {
        display: block;
        position: fixed;
        top: 5px;
        right: 10px;
        font-size: 25px;
    }
}

@keyframes SidebarMove600 {
    100% {
        right: 0vw;
    }
}