.SideBar .link {
    display: flex;
    align-items: center;
    font-family: Poppins;
    transition: .5s;
    color: #72676e;
    padding: 10px 20px;
    border-radius: 10px;
    text-decoration: none;
    margin-bottom: 5px;
    cursor: pointer;
    white-space: nowrap;
}

.SideBar .link:hover {
    background-color: #d8e0e8;
}

.SideBar .activeLink {
    background-color: #135bcc !important;
    color: #f2f4f8 !important;
}

.SideBar .DropDowns {
    border: 1px solid lightgray;
    border-radius: 10px;
}

.SideBar .DropDowns > a:last-child {
    margin-bottom: 0 !important;
}